import './index.pcss'

function ajax(type, url, callback) {
  var xmlhttp = new XMLHttpRequest()
  xmlhttp.open(type, url)
  xmlhttp.setRequestHeader('Content-Type', 'application/json')
  xmlhttp.setRequestHeader('Accept', '*/*')
  xmlhttp.setRequestHeader('Accept-Language', 'zh-CN,zh;q=0.8')
  xmlhttp.send('{}')

  xmlhttp.onreadystatechange = function() {
    if (xmlhttp.readyState === XMLHttpRequest.DONE) {
      console.log(JSON.parse(xmlhttp.response).data)
      callback(JSON.parse(xmlhttp.response).data)
    }
  }
}

ajax('post', 'https://dxapi.youxiang0210.com/v2/mobile/constant/help', function(_data) {
  var data = _data.helpList
  console.log(data)
  var html = '<ul>'
  for (var i = 0; i < data.length; i += 1) {
    html +=
      `<li>
        <div class='title'> ${data[i].question} </div>
        <div class='content'> ${data[i].answer}  </div>
      </li>`
  }
  html += '</ul>'

  document.getElementById('help').innerHTML = html
})



